import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-submit-form',
  templateUrl: './submit-form.component.html',
  styleUrls: ['./submit-form.component.css']
})
export class SubmitFormComponent implements OnInit {
  uploadFiles:any[];
  fileSrcs:string[] = new Array();
  num:number;
  stuffName:string="";
  stuffPrice:number=0;
  stuffDetail:string="";
  stuffKind:string="";
  organization:number=0;
  telephone:number;
  
  constructor() {
   }
  uploadChange(files){
  	console.log(this.fileSrcs);
  	this.fileSrcs.push(window.URL.createObjectURL(files[0]));
  	this.num = this.fileSrcs.length;
  	console.log(this.num);
  }
  onSubmit(){
  	var JsonForm = {
  		"stuffName":this.stuffName,
  		"stuffPrice":this.stuffPrice,
  		"stuffDetail":this.stuffDetail,
  		"stuffKind":this.stuffKind,
  		"organization":this.organization,
  		"telephone":this.telephone,
  		"pictures":this.fileSrcs
  	}
  	console.log(JsonForm);
  }
  ngOnInit() {
  }

}



